<template>
  <v-ons-page>
    <div class="line">제목 : <v-ons-input class="input_box" v-model="qTitle"></v-ons-input></div>
    <v-ons-input class="content_box" aria-placeholder="문의 내용을 입력해주세요." v-model="qContent"></v-ons-input>
    <v-ons-button class="submit_button" v-on:click="submitQ">전송</v-ons-button>
  </v-ons-page>
</template>

<script>
import { question } from '../../../api/index';

export default {
  key: "QuestionPage",
  name: "Question",
  toolBarName: "문의하기",
  data() {
    return {
      qTitle: '',
      qContent: '',
      get id() {
        return window.localStorage.getItem("id");
      }
    }
  },
  methods: {
    submitQ: function () {
      if(this.qTitle === "") {
        this.$ons.notification.alert("제목을 입력해주세요.");
        return;
      }

      if(this.qContent === "") {
        this.$ons.notification.alert("문의 내용을 입력해주세요.");
        return;
      }

      var quesObj = {
        USER_IDX: this.id,
        TITLE: this.qTitle,
        CONTENT: this.qContent
      }
      question(quesObj).then(() => {
        this.$store.commit("goToMain");
      })
    }
  }
}
</script>

<style>
.line {
  width: 100%;
  text-align: center;
  line-height: 20px;
  vertical-align: center;
  position: absolute;
  top: 7%;
}
.input_box {
  width: 75%;
  border: 1px solid;
  border-radius: 5px;
}
.content {
  position: relative;
  text-align: center;
  width: 90%;
  height: 60%;
  bottom: 10%;
  overflow: none;
}

.content_box {
  width: 90%;
  height: 70%;
  border: 1px solid;
  display: block;
  position: absolute;
  top: 15%;
  overflow: scroll;
  border-radius: 5px;
  left: 5%;
}

.submit_button {
  position: absolute;
  bottom: 3%;
  right: 5%;
}
</style>


